<template>
  <div>
    <div class="info">
      <div class="container">
        <GoodsMedia :goods="goods" :src="src"></GoodsMedia>
        <div class="spec">
          <GoodsName :goods="goods"></GoodsName>
          <GoodsSku :skus="skus" @getSku="getSku"></GoodsSku>
          <div class="count">
            <div class="label">数量</div>
            <el-input-number
              v-model="count"
              :min="1"
              size="small"
            ></el-input-number>
          </div>
          <el-button type="primary" @click="addCart">加入购物车</el-button>
        </div>
      </div>
    </div>
    <GoodsDetail :attrs="attrs" :image="image"></GoodsDetail>
  </div>
</template>

<script>
import GoodsSku from "./components/GoodsSku.vue";
import GoodsName from "./components/GoodsName.vue";
import GoodsMedia from "./components/GoodsMedia.vue";
import GoodsDetail from "./components/GoodsDetail.vue";

export default {
  components: {
    GoodsSku,
    GoodsName,
    GoodsMedia,
    GoodsDetail,
  },
  props: ["id"],
  data() {
    return {
      goods: {},
      src: "",
      skus: [],
      attrs: [],
      image: [],
      skuId: null,
      count: 1,
    };
  },
  methods: {
    async getGoods() {
      const { data } = await this.$axios.get("/goods", {
        params: { id: this.id },
      });
      this.goods = data.result;
      this.src = data.result.mainPictures[0];
      this.skus = data.result.skus;
      this.attrs = data.result.details.properties;
      this.image = data.result.details.pictures;
    },
    getSku(data) {
      // console.log(data);
      this.skuId = data.id;
      this.goods.price = data.price;
      this.goods.oldPrice = data.oldPrice;
    },
    async addCart() {
      if (!this.$store.state.userInfo.token) {
        this.$message.warning("请先登录您的账号");
      } else if (this.skuId === null) {
        this.$message.error("请选择商品规格");
      } else {
        const { data } = await this.$axios.post("/member/cart", {
          skuId: this.skuId,
          count: this.count,
        });
        // console.log(data);
        this.$message.success("加入购物车成功");
      }
    },
  },
  created() {
    this.getGoods();
  },
  watch: {
    id: {
      handler() {
        this.getGoods();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.info {
  margin: 30px 0;
  min-height: 600px;
  .container {
    background: #ffffff;
    display: flex;
    .spec {
      padding: 30px 50px;
      .count {
        margin: 20px 0;
        display: flex;
        align-items: center;
        .label {
          width: 60px;
          color: #909399;
          padding-left: 10px;
        }
      }
      .el-button {
        width: 180px;
        height: 50px;
        font-size: 16px;
      }
    }
  }
}
</style>

