<template>
  <div class="sku">
    <dl>
      <dd
        v-for="(item, index) in skus"
        :key="index"
        :class="{ active: active === index }"
        @click="select(index, item)"
      >
        <span v-for="(subitem, index) in item.specs" :key="index">{{
          subitem.valueName
        }}</span>
      </dd>
    </dl>
  </div>
</template>

<script>
export default {
  props: ["skus"],
  data() {
    return {
      active: null,
    };
  },
  methods: {
    select(index, sku) {
      this.active = index;
      // console.log(data);
      this.$emit("getSku", sku);
    },
  },
};
</script>

<style scoped lang="less">
.sku {
  margin: 20px 0;
  p {
    margin-right: 20px;
  }
  dl {
    display: flex;
    flex-wrap: wrap;
    dd {
      color: #606266;
      margin-right: 10px;
      margin-bottom: 10px;
      padding: 10px 20px;
      border: 1px solid #dcdfe6;
      cursor: pointer;
      &:hover,
      &.active {
        border-color: #409eff;
      }
      &.disabled {
        opacity: 0.6;
        border-style: dashed;
        cursor: not-allowed;
      }
    }
  }
}
</style>